import { computed, reactive, toRefs } from 'vue';
import { useTranslate } from '@demo/use-translate';
import { zhCNData } from './data-zh';
import { enUSData } from './data-en';
import { deepClone } from '../../utils/deep-clone';
const i18n = {
    'zh-CN': {
        showBadge: '徽标提示',
        radioMode: '单选模式',
        multipleMode: '多选模式',
        customContent: '自定义内容',
        data: zhCNData,
        dataSimple: [{ text: '分组 1' }, { text: '分组 2' }],
    },
    'en-US': {
        showBadge: 'Show Badge',
        radioMode: 'Radio Mode',
        multipleMode: 'Multiple Mode',
        customContent: 'Custom Content',
        data: enUSData,
        dataSimple: [{ text: 'Group 1' }, { text: 'Group 2' }],
    },
};
export default {
    setup() {
        const t = useTranslate(i18n);
        const state = reactive({
            activeId: 1,
            activeId2: 1,
            activeIds: [1, 2],
            activeIndex: 0,
            activeIndex2: 0,
            activeIndex3: 0,
            activeIndex4: 0,
        });
        const items = computed(() => t('data'));
        const simpleItems = computed(() => t('dataSimple'));
        const badgeItems = computed(() => {
            const data = deepClone(t('data')).slice(0, 2);
            data[0].dot = true;
            data[1].badge = 5;
            return data;
        });
        return {
            ...toRefs(state),
            t,
            items,
            badgeItems,
            simpleItems,
        };
    },
};
